import React from 'react';

class Navigation extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            links: this.props.pages,
        }
    }

    generateKey = (pre) => {
        return `${pre}_${new Date().getTime()}`;
    }

    render() {

        return (
            <nav>
                <div className="nav-wrapper">
                    <a href="{ this.props.pages.home.linkTo }" className="brand-logo center">UMEET</a>
                    <ul className="left hide-on-med-and-down">
                        {
                            this.state.links.map(page => {
                                return (
                                    <li {...page[1] === true ? (
                                        className="active"
                                    ) : ( className="not-active" )} key={this.generateKey(page[0])}>
                                        <a href={page[2]}>
                                            {page[0]}
                                        </a>
                                    </li>
                                )
                            })
                        }
                    </ul>
                </div>
            </nav>
        )
    }
}

export default Navigation;